.header {
  display: flex;
  background-color: #141414;
  padding: 10px 0;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  z-index: 1000;
  position: fixed;
  top: 0;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.3);
}

.logo {
  font-size: 26px;
  font-weight: bold;
  color: #e50914;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.8);
  margin-left: 20px;
}

.logo a {
  color: inherit;
  text-decoration: none;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 20px;
  margin: 0;
  padding: 0;
  align-items: center;
}

.nav-links li a {
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  padding: 10px 15px;
  transition: color 0.3s ease, transform 0.3s ease;
  border-radius: 4px;
}

.nav-links li a:hover {
  background-color: #e50914;
  transform: scale(1.1);
  box-shadow: 0px 4px 8px rgba(229, 9, 20, 0.4);
}

.menu-icon {
  display: none;
  font-size: 26px;
  color: #fff;
  cursor: pointer;
  margin-right: 20px;
}

@media (max-width: 768px) {
  .nav-links {
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.9);
    position: absolute;
    top: 60px;
    right: 0;
    width: 200px;
    display: none;
    padding-top: 20px;
    padding-bottom: 20px;
    border-radius: 8px;
  }

  .nav-links.active {
    display: flex;
  }

  .nav-links li {
    text-align: center;
    padding: 10px 0;
    border-bottom: 1px solid #333;
  }

  .nav-links li a {
    padding: 15px 10px;
    font-size: 18px;
  }

  .menu-icon {
    display: block;
  }
}
