.movie-card {
  position: relative;
  width: 240px;
  height: auto;
  flex: 0 0 auto;
  margin-right: 10px;
  transition: transform 0.3s ease, z-index 0.3s ease;
  cursor: pointer;
  border-radius: 8px;
  overflow: hidden;
  background-color: #333;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.movie-image {
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 360px;
  border-radius: 8px;
  position: relative;
}

.movie-hover {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  border-radius: 8px;
}

.movie-card:hover .movie-hover,
.movie-hover.expanded {
  opacity: 1;
  height: auto;
}

.movie-content {
  text-align: center;
  width: 100%;
  padding: 0 10px;
}

.movie-title {
  font-size: 18px;
  font-weight: bold;
  color: #fff;
  margin-bottom: 10px;
  word-wrap: break-word;
}

.movie-description {
  font-size: 14px;
  color: #fff;
  line-height: 1.4;
  max-height: 60px; /* Limita l'altezza per l'anteprima a circa tre righe */
  overflow: hidden;
  text-overflow: ellipsis; /* Mostra i puntini di sospensione */
  white-space: normal; /* Permette il ritorno a capo */
  display: -webkit-box;
  -webkit-line-clamp: 3; /* Limita il testo a tre righe */
  -webkit-box-orient: vertical;
  transition: max-height 0.5s ease, opacity 0.5s ease;
}

.movie-description.expanded {
  max-height: 150px; /* Altezza massima per il testo espanso */
  white-space: normal; /* Permette il ritorno a capo normale */
  overflow-y: auto; /* Abilita lo scrolling verticale per il testo completo */
  text-overflow: initial; /* Rimuove i puntini di sospensione nella vista espansa */
  -webkit-line-clamp: unset; /* Rimuove il limite di righe */
}

.movie-description.expanded::-webkit-scrollbar {
  width: 6px;
}

.movie-description.expanded::-webkit-scrollbar-thumb {
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 10px;
}

.movie-buttons {
  display: flex;
  gap: 10px;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.movie-card:hover .movie-buttons,
.movie-hover.expanded .movie-buttons {
  opacity: 1;
}

.movie-buttons .btn {
  padding: 10px 20px;
  border-radius: 4px;
  color: white;
  font-size: 0.9rem;
  cursor: pointer;
  display: flex;
  align-items: center;
  transition: background-color 0.3s ease; /* Mantiene solo il cambio di colore */
  border: none;
  background-color: rgba(255, 255, 255, 0.3);
}

.movie-buttons .btn svg {
  margin-right: 6px;
}

.movie-buttons .btn.play-btn {
  background-color: #e50914;
}

.movie-buttons .btn.play-btn:hover {
  background-color: #f40612;
}

.movie-buttons .btn.info-btn {
  background-color: rgba(255, 255, 255, 0.3);
}

.movie-buttons .btn.info-btn:hover {
  background-color: rgba(255, 255, 255, 0.6);
}
@media (max-width: 768px) {
  .movie-card {
    width: 180px;
  }

  .movie-image {
    height: 270px;
  }

  .movie-title {
    font-size: 16px;
  }

  .movie-description {
    font-size: 12px;
  }

  .movie-buttons .btn {
    font-size: 0.8rem;
    padding: 8px 16px;
  }
}