* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.footer {
  background-color: #181818; /* Colore scuro per il footer */
  color: #f1f1f1; /* Testo chiaro per un buon contrasto */
  text-align: center;
  padding: 40px 0;
  width: 100%;
  margin: 0; /* Elimina eventuali margini che causano spazio bianco */
  box-shadow: 0 -2px 6px rgba(0, 0, 0, 0.7); /* Ombra per separare il footer dalla pagina */
  position: relative; /* Assicura che il footer si adatti correttamente */
}

.footer-content {
  max-width: 1200px;
  margin: 0 auto;
}

.footer p {
  margin: 0;
  font-size: 14px;
  margin-bottom: 20px;
  color: #b3b3b3; /* Colore leggermente più chiaro per il testo del copyright */
}

.footer-links,
.footer-social {
  margin: 20px 0;
}

.footer-links a,
.footer-social a {
  color: #f1f1f1;
  text-decoration: none;
  margin: 0 15px;
  font-size: 16px; /* Font un po' più grande per migliorare la leggibilità */
  transition: color 0.3s ease, transform 0.3s ease; /* Aggiunta di una transizione per l'interattività */
}

.footer-links a:hover,
.footer-social a:hover {
  color: #e50914; /* Colore rosso simile a Netflix al passaggio del mouse */
  transform: scale(1.1); /* Leggero ingrandimento al passaggio del mouse */
}

.footer-social a:hover {
  text-decoration: underline;
}

/* Effetto linea sotto i link al passaggio del mouse */
.footer-links a::before,
.footer-social a::before {
  content: '';
  display: block;
  height: 2px;
  width: 0;
  background-color: #e50914; /* Linea rossa sotto i link */
  transition: width 0.3s ease;
}

.footer-links a:hover::before,
.footer-social a:hover::before {
  width: 100%; /* Espande la linea sotto il testo al passaggio del mouse */
}

/* Personalizzazione delle icone social */
.footer-social a {
  margin: 0 20px;
}

/* Responsive design per dispositivi più piccoli */
@media screen and (max-width: 768px) {
  .footer {
    padding: 20px 0;
  }
  
  .footer-links a,
  .footer-social a {
    font-size: 14px;
    margin: 0 10px;
  }
}

/* Responsive design per dispositivi molto piccoli (fino a 400px) */
@media screen and (max-width: 400px) {
  .footer {
    padding: 15px 0; /* Riduci il padding per adattarlo agli schermi più piccoli */
  }
  
  .footer p {
    font-size: 12px; /* Riduci la dimensione del testo */
  }

  .footer-links a,
  .footer-social a {
    font-size: 12px; /* Riduci il font per i link */
    margin: 0 5px;   /* Riduci lo spazio tra i link */
  }

  .footer-social a {
    margin: 0 10px;  /* Mantieni un po' di spazio tra le icone social */
  }

  .footer-links,
  .footer-social {
    margin: 10px 0;  /* Riduci lo spazio tra le sezioni */
  }
}
