.home-page {
  color: #fff;
  background-color: #141414;
  padding-top: 60px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 10px;
}

.hero {
  position: relative;
  height: 70vh;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding-left: 40px;
  color: white;
  text-align: left;
  overflow: hidden;
}

.hero-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: url("https://ntvb.tmsimg.com/assets/p22728770_b_h8_aa.jpg?w=1280&h=720") no-repeat center center/cover;
  z-index: 0;
  transition: opacity 0.3s ease;
}

.hero-video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: 0;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
}

.hero:hover .hero-video {
  opacity: 1;
}

.hero:hover .hero-image {
  opacity: 0;
}

.hero-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(to right, rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.4));
  z-index: 1;
  opacity: 1;
  transition: opacity 0.3s ease;
}

.hero:hover .hero-overlay {
  background: none;
}

.hero-content {
  max-width: 600px;
  z-index: 2;
  position: relative;
  transition: all 0.3s ease;
}

.hero-title {
  font-size: 4rem;
}

.hero-subtitle {
  font-size: 1.5rem;
  color: #46d369;
}

.hero-description {
  font-size: 1.2rem;
  margin-bottom: 20px;
  line-height: 1.4;
}

.hero-buttons {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.hero-buttons .btn {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 25px;
  font-size: 1.2rem;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  min-width: 150px;
}

.play-btn {
  background-color: #e50914;
  color: #fff;
  border: none;
}

.play-btn:hover {
  background-color: #f40612;
}

.info-btn {
  background-color: rgba(109, 109, 110, 0.7);
  color: #fff;
  border: none;
}

.info-btn:hover {
  background-color: rgba(109, 109, 110, 1);
}


.audio-notification {
  position: absolute;
  top: 20px; /* Sposta in alto a destra */
  right: 20px;
  z-index: 10;
  display: flex;
  align-items: center;
  color: white;
}


.mute-indicator, .unmute-indicator {
  display: flex;
  align-items: center;
  gap: 10px;
}

@media (max-width: 768px) {
  /* Riduci altezza della hero */
  .hero {
    height: 60vh;
    justify-content: center; /* Centra contenuto */
    text-align: center; /* Centra testo */
    padding-left: 0; /* Rimuove padding laterale */
  }

  /* Riduci font-size per schermi piccoli */
  .hero-title {
    font-size: 30px;
    text-align: center; /* Centra il titolo */
  }

  .hero-subtitle {
    font-size: 20px;
    text-align: center; /* Centra il sottotitolo */
  }

  /* La descrizione sarà più ristretta */
  .hero-description {
    font-size: 14px;
    margin-right: auto;
    margin-left: auto;
    max-width: 80%; /* Restringi la descrizione */
  }

  /* Pulsanti uno sopra l'altro e centrali */
  .hero-buttons {
    flex-direction: column;
    align-items: center; /* Centra i pulsanti */
    gap: 10px;
  }

  .hero-buttons .btn {
    font-size: 0.9rem; /* Riduci font-size */
    padding: 10px 20px; /* Riduci padding */
    min-width: 140px; /* Riduci larghezza minima */
    width: 100%; /* Pieno in larghezza */
    max-width: 200px; /* Limita la larghezza massima */
    text-align: center; /* Centra il testo */
  }

  .play-btn {
    background-color: #e50914;
    color: #fff;
    border: none;
  }

  .info-btn {
    background-color: rgba(109, 109, 110, 0.7);
    color: #fff;
    border: none;
  }

  /* Posiziona la notifica audio in alto a destra per versione desktop */
  .audio-notification {
    top: 20px;
    right: 20px;
    bottom: auto;
    left: auto;
    position: absolute;
  }
}

@media (max-width: 680px) {
  /* Riduci altezza della hero */
  .hero {
    height: 50vh; /* Riduci ulteriormente l'altezza */
    justify-content: center;
    padding-left: 0;
    padding-right: 0;
    text-align: center;
  }

  /* Centra tutto il contenuto */
  .hero-title,
  .hero-subtitle {
    text-align: center;
  }

  /* Pulsanti centrali e più piccoli */
  .hero-buttons {
    flex-direction: column;
    align-items: center; /* Centra i pulsanti */
    gap: 8px; /* Riduci il gap tra i pulsanti */
  }

  .hero-buttons .btn {
    font-size: 0.8rem; /* Riduci ulteriormente il font-size */
    padding: 8px 12px; /* Riduci padding */
    min-width: 110px; /* Riduci larghezza minima */
    max-width: 160px; /* Limita la larghezza massima */
    width: 100%; /* Pieno in larghezza */
  }

  /* Centra la descrizione e riduci dimensioni */
  .hero-description {
    font-size: 12px;
    max-width: 90%; /* Ancora più ristretta su schermi piccoli */
    margin-left: auto;
    margin-right: auto;
  }
}