.movie-list {
  margin: 40px 0;
  position: relative;
  padding: 0 8px;
  overflow: hidden;
}

.movie-list-grid {
  display: flex;
  overflow-x: auto;
  padding: 10px 0px;
  scrollbar-width: none;
  scroll-behavior: smooth;
}

.movie-list-grid::-webkit-scrollbar {
  display: none;
}

/* Stile migliorato per i pulsanti del carosello */
.carousel-btn {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: rgba(0, 0, 0, 0.6);
  border: none;
  color: white;
  padding: 15px;
  cursor: pointer;
  z-index: 1;
  transition: background-color 0.3s ease, transform 0.2s ease;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2); /* Aggiunge ombra */
}

.carousel-btn span {
  font-size: 24px;
}

/* Aggiunge un'ombra più pronunciata e un'animazione al passaggio del mouse */
.carousel-btn:hover {
  background-color: rgba(255, 0, 0, 0.8);
  box-shadow: 0px 6px 14px rgba(0, 0, 0, 0.3); /* Ombra maggiore */
  transform: translateY(-50%) scale(1.1); /* Leggera espansione al passaggio del mouse */
}

/* Pulsante sinistro */
.left-btn {
  left: 10px;
}

/* Pulsante destro */
.right-btn {
  right: 10px;
}

/* Stile per i pulsanti al passaggio del mouse */
.left-btn:hover,
.right-btn:hover {
  background-color: rgba(255, 0, 0, 0.8);
}
